<template>
  <div class="arbitrament_box">
    <h4>仲裁详情</h4>
    <el-divider />
    <span class="fs-sm">仲裁编号：{{ arbitramentData.code }}</span>
    <span class="time">发起时间：{{ arbitramentData.createTime }}</span>
    <div class="type_box">
      <div class="type_box_item">
        <h4>仲裁类型：</h4>
        <!-- 仲裁类型 0-仅退款 1-退款退货 2-退换 -->
        <span class="text">
          {{ arbitramentData.arbitramentType === 0 ? '仅退款' : arbitramentData.arbitramentType === 1 ? '退款退货' : arbitramentData.arbitramentType === 2 ? '退换' : '' }}
        </span>
      </div>
      <div class="call">
        <el-button type="text">联系买家</el-button>
        <el-button type="text">联系卖家</el-button>
      </div>
    </div>
    <label class="description">描述：</label><span class="fs-sm">{{ arbitramentData.describes }}</span>
    <div class="img_group">
      <label class="fs-sm">凭证：</label>
      <el-image v-for="(item, i) in arbitramentData.voucherUrl" :key="i" class="img" :src="item" />
    </div>
    <h4 class="title">订单详情</h4>
    <el-divider />
    <span class="order_item">订单编号：</span>
    <span class="order_item">下单时间：</span>
    <span class="order_item">付款时间：</span>
    <el-table :data="orderItemData" class="table">
      <el-table-column prop="prodName" label="商品" align="center" />
      <el-table-column prop="itemSpec" label="商品属性" align="center" />
      <el-table-column prop="price" label="单价" align="center" />
      <el-table-column prop="count" label="数量" align="center" />
      <!-- 订单状态 0-待付款 1-待发货 2-待收货 3-已签收/待评价 4-退款中 5-待拼团 9-交易关闭 10-交易完成 小超状态0-代付款10-完成 -->
      <el-table-column label="状态" align="center">
        <template>
          <span v-if="orderHeader.orderStatus === 0">待付款</span>
          <span v-else-if="orderHeader.orderStatus === 1">待发货</span>
          <span v-else-if="orderHeader.orderStatus === 2">待收货</span>
          <span v-else-if="orderHeader.orderStatus === 3">已签收</span>
          <span v-else-if="orderHeader.orderStatus === 4">退款中</span>
          <span v-else-if="orderHeader.orderStatus === 9">交易关闭</span>
          <span v-else-if="orderHeader.orderStatus === 10">交易完成</span>
        </template>
      </el-table-column>
      <el-table-column label="商品总价" align="center">
        <template>
          <span>￥{{ orderHeader.totalPrice }}</span>
        </template>
      </el-table-column>
    </el-table>
    <div class="pay">实付款：<span class="money">{{ orderHeader.totalPrice }}.00元</span></div>
    <div class="btn_group">
      <el-button class="inquire" round @click="onAgree">买家胜诉</el-button>
      <el-button class="error_btn" round @click="onDelete">驳回仲裁</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ArbitrationContent',

  data() {
    return {
      arbitramentData: {}, // 仲裁订单的详情
      orderItemData: [], // 明细
      orderHeader: {}, // 订单详情
    }
  },

  methods: {
    // 点击买家胜诉
    onAgree () {
      this.apiArbitramentAgree(this.arbitramentData.id)
    },
    // 点击驳回仲裁
    onDelete () {
      this.apiArbitramentDelete(this.arbitramentData.id)
    },

    // api接口-----------------------------------
    // 根据id查询
    apiArbitramentFindById () {
      this.$http.get(`/woho/product/arbitrament/findById/${this.$route.query.id}`)
        .then((res) => {
          if (res.data.success) {
            this.arbitramentData = res.data.data.arbitrament
            this.orderHeader = res.data.data.orderHeader
            console.log(this.orderHeader, 'this.orderHeader')
            this.arbitramentData.voucherUrl = res.data.data.arbitrament.voucherUrl.split(',')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 查明细详情
    apiOrderItemFindById () {
      this.$http.post(`/woho/order/item/findByOrderId/${this.$route.query.orderId}`)
        .then((res) => {
          console.log(res)
          if (res.data.success) {
            this.orderItemData = res.data.data
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 同意仲裁
    apiArbitramentAgree (id) {
      this.$http.get(`/woho/product/arbitrament/agree/${id}`)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('买家胜诉')
            this.$router.push('/adminOrder/arbitrationOrderList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 仲裁驳回
    apiArbitramentDelete (id) {
      this.$http.get(`/woho/product/arbitrament/delete/${id}`)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('驳回仲裁')
            this.$router.push('/adminOrder/arbitrationOrderList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
  },

  created () {
    this.apiArbitramentFindById()
    setTimeout(() => {
      this.apiOrderItemFindById()
    }, 100)
  }

}
</script>

<style lang="scss" scoped>
  label {
    display: inline-block;
    width: 50px;
  }
  .arbitrament_box{
    width: 100%;
    margin: auto;
    .time{
      font-size: 14.7696px;
      margin-left: 187px;
    }
    .type_box{
      width: 100%;
      height: 145px;
      background-color: #e6fffc;
      border-radius: 5px;
      border: solid 1px #5bd9be;
      margin-top: 15px;
      padding-left: 15px;
      .type_box_item{
        display: flex;
        align-items: center;
        margin-top: 15px;
        .text{
          color: #e7a53d;
          font-size: 14.7696px;
        }
      }
      .call{
        margin-top: 5px;
        width: 150px;
        display: flex;
        justify-content: space-between;
      }

    }
    .description{
      font-size: 14.7696px;
      margin: 30px 0px;
    }
    .img_group{
      display: flex;
      align-items: flex-start;
      .img{
        margin-right: 15px;
        width: 133px;
        height: 133px;
      }
    }
    .title{
      margin-top: 90px;
    }
    .order_item{
      margin-right: 150px;
      font-size: 14.7696px;
      color: #666666;
    }
    .table{
      margin-top: 15px;
    }
    .pay{
      width: 100%;
      text-align: right;
      margin-top: 15px;
      .money{
        color: #f9930f;
      }
    }
  }
  .btn_group{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 50px;
    .error_btn{
      margin-left: 60px;
    }
  }
</style>
